body {
    background: url(../../Vers√£o\ 2/table-bg.jpg);
    background-repeat: no-repeat;
    background-size: auto;
    width: 100%;
    height: 100%;
    font-family: 'Rubik';
    margin: 0 auto;
    display: flow-root;
}

.container {
    display: flex;
    height: 100vh;
    flex-direction: column;
   
    width: 95%;
    margin: 0 auto;
    
}

.container h2 {
    margin: 25px 0;
    color: #72EFDB;
    font-size: 33px;
    text-align: center;
    letter-spacing: 0px;
    margin-top: 100px;
}

.buttonsPages {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.buttonsPages button {
    color: #18326B;
    background-color: #72EFDB;
    padding: 2px 5px;
    border-radius: 8px;
    margin: 2px;
}

.list {
    background-color: #18326B;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
}

.listMenu {
    border-radius: 14px 14px 0px 0px;
    background: #0A1744;
    display: flex;
    justify-content: space-between;
    padding: 12px 55px 12px 0px;
    font-weight: bold;
}

.listMenuTop {
    display: flex;
    margin: 0 15px;
    margin-top: 10px;
    font-size: 17px;
    color: #727272;
}

.dateTime {
    display: flex;
    margin-top: 10px;
    
}

