.contentList {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #5985b566;
    margin: 0 15px;
    font-size: 14px;
    font-weight: regular;
    color: #72EFDB;
    padding: 15px 0px;
}

/* .datesTimes {
    display: flex;
    width: 40%;
} */

.listDate {
    display: flex;
    
}

.hashtag {
    color: #72EFDB;
    font-size: 17px;
}

/* .hashtagName {
    color: white;
} */

.listHashtag {
    color: white;
}

/* .listItems .listMenuTop {
    padding: 15px;
} */

.contentList .listMenuTop {
    padding: 15px;
}

.date {
    color: #72EFDB;
    font-size: 17px;
}

.time {
    color: #72EFDB;
    font-size: 17px;
    margin: 0px 0px 0px 50px;
}

/* .dates {
    padding: 0px 0px 0px 5px;
} */

.contentBodyList {
    display: flex;
}

.listDate {
    padding: 0px 0px 0px 5px;
}

/* .times {
    padding: 0px 40px 0px 50px;
} */

.listHour {
    padding: 0px 40px 0px 40px;
}


/* -------------------vers√£o desktop----------------------*/

@media (min-width: 1024px) {
    
    .container {
        width: 80%;
        margin: 0 auto;
    }

    .container h2 {
        margin: 25px 0;
        color: #72EFDB;
        font-size: 40px;
        text-align: left;
        letter-spacing: 0px;
        margin-top: 18%;
    }

    .buttonsPages {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
    }
    
    .buttonsPages button {
        color: #18326B;
        background-color: #72EFDB;
        padding: 2px 5px;
        border-radius: 8px;
        height: 39px;
        width: 26px;
        font-size: 20px;
    }

    .setCurrentPage {
        color: magenta;
    }

    .listMenu {
        border-radius: 14px 14px 0px 0px;
        padding: 34px 0px 34px 0px;
    }

    .listBackground{
        background: #18326B 0% 0% no-repeat padding-box;
        border-radius: 14px;
        opacity: 1;
        backdrop-filter: blur(30px);
    }

    .hashtag {
        font-size: 24px;
        padding: 0px 0px 0px 65px;
    }

    /* .hashtagName {
        padding: 0px 0px 0px 65px;
    } */

    .listHashtag {
        padding: 0px 0px 0px 65px;
    }

    .date {
        font-size: 24px;
    }

    .time {
        font-size: 24px;
    }

    /* .listItems {
        font-size: 24px;
    } */

    .contentList {
        font-size: 24px;
    }
    
    .dateTime {
        display: flex;
        margin-top: 10px;
        margin-right: 65px;
        
    }

    /* .datesTimes {
        display: flex;
        width: 20%;
    } */

    .listDate {
        display: flex;
        width: 20%;
    }

    .listHour {
        padding: 0px 40px 0px 70px;
    }

    .contentBodyList {
        display: flex;
        justify-content: space-between;
    }

}

@media (min-width: 1300px) {

    .container h2 {
        margin-top: 10%;
    }
}